<template>
    <div id="program">
        <h1>Programs (channel {{currentChannel[0]}})</h1>

        <input v-on:keyup="search" v-on:keydown.38.40="selectResult" type="text" placeholder="SÃ¶k..." v-model="searchValue"/>
        <div style="position: relative">
            <div id="searchResults">
                <div v-for="program in searchResults" :key="'s' + program.id" :pid="program.id" class="result">
                    <div v-on:click="resultClicked(program)">{{program.name}}</div>
                </div>
            </div>
        </div>
        <hr/>

        <div v-for="program in programs" :key="program.id" class="program" v-on:click="resultClicked(program)">
            <div>{{program.name}}</div>
            <div><img alt="program" :src="program.programimage"/></div>
        </div>
    </div>
</template>

<script>
import dataStore from '../store/dataStore';

export default {
    data: function() {
        return {
            programs: dataStore.data.programs,
            favourites: dataStore.data.favouritePrograms,
            currentChannel: dataStore.data.currentChannel,
            searchResults: [],
            searchValue: ""
        };
    },
    methods: {
        search: function(event) {
            if((event.keyCode >= 37 && event.keyCode <= 40)) { return; }

            var searchResults = document.getElementById("searchResults");
            var selected = searchResults.getElementsByClassName("selected");

            if(event.keyCode == 13) {
                if(selected.length <= 0) { return; }
                var pid = selected[0].getAttribute("pid");
                var program = this.programs.find((program) => { return program.id == pid; });

                if(dataStore.methods.isNull(program)) { return; }
                this.resultClicked(program);
                return;
            }

            var searchVal = this.searchValue.toLowerCase();

            //Deselect all.
            for(var i = 0; i < selected.length; i++) {
                selected[i].classList.remove("selected");
            }

            if(searchVal.length <= 0) {
                this.searchResults = [];
                document.getElementById("searchResults").classList.remove("visible");
                return;
            }

            this.searchResults = this.programs.filter((program) => {
                return program.name.toString().toLowerCase().indexOf(searchVal) >= 0;
            });

            if(this.searchResults.length > 0) {
                document.getElementById("searchResults").classList.add("visible");
            }
            else {
                document.getElementById("searchResults").classList.remove("visible");
            }
        },
        selectResult: function(event) {
            var searchResults = document.getElementById("searchResults");
            var results = searchResults.getElementsByClassName("result");
            var selected = searchResults.getElementsByClassName("selected");

            var resultUndefinedIndex = event.keyCode == 38 ? results.length - 1 : 0;

            if(selected.length <= 0) {
                if(results.length > 0) {
                    results[resultUndefinedIndex].classList.add("selected");
                }
            }
            else {
                var next = selected[0];
                while((next = (event.keyCode == 40
                                ? next.nextElementSibling
                                : next.previousElementSibling)) != null && 
                      !next.classList.contains("result"));

                //Deselect all.
                for(var i = 0; i < selected.length; i++) {
                    selected[i].classList.remove("selected");
                }

                //Select next.
                if(next != null) {
                    next.classList.add("selected");
                }
                else {
                    results[resultUndefinedIndex].classList.add("selected");
                }
            }

            event.preventDefault();
        },
        resultClicked: function(program) {
            if(dataStore.methods.isNull(program) || dataStore.methods.isNull(program.id)) { return; }

            //Change program.
            dataStore.methods.changeProgram(program.id);

            //Hide menu.
            document.getElementById("searchResults").classList.remove("visible");

            //Deselect all.
            var searchResults = document.getElementById("searchResults");
            var selected = searchResults.getElementsByClassName("selected");
            
            for(var i = 0; i < selected.length; i++) {
                selected[i].classList.remove("selected");
            }
        }
    },
    created: async function() {
        await dataStore.methods.changeChannel(dataStore.data.currentChannel[0]);
    }
}
</script>

<style scoped>
    .program img
    {
        width: 75px;
    }

    #searchResults
    {
        position: absolute;
        display: none;
        top: 0px;
        left: 50%;
        width: 200px;
        margin: 8px auto 8px auto;
        transform: translateX(-50%);
        border: 1px solid #AAAAAA;
        background: #FFFFFF;
        text-align: left;
        box-sizing: border-box;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    #searchResults.visible
    {
        display: block;
    }

    #searchResults > .result,
    #searchResults > .result > div
    {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    #searchResults > .result:not(:first-child):not(:last-child)
    {
        margin: 6px 0px 6px 0px;
    }

    #searchResults > div.selected
    {
        background: #000099;
        color: #FFFFFF;
    }
</style>