<template>
    <div id="podfile">
        <h1>Podfiles (program {{currentProgram[0]}})</h1>
        <div v-for="podfile in podfiles" :key="podfile.id" class="podfile">
            <h2>{{podfile.title}}</h2>
            <p>{{podfile.description}}</p>
            <audio controls>
                <source :src="podfile.url" type="audio/mpeg"/>
                Your browser does not support the &lt;audio&gt; tag.
            </audio>
        </div>
    </div>
</template>

<script>
import dataStore from "../store/dataStore";

export default {
    data: function() {
        return {
            currentProgram: dataStore.data.currentProgram,
            podfiles: dataStore.data.podfiles
        }
    }
}
</script>

