const dataStore = {
    data: {
        favouriteChannels: (window.localStorage.getItem("favouriteChannels") || "").split(","),
        currentChannel: [132],
        favouritePrograms: (window.localStorage.getItem("favouritePrograms") || "").split(","),
        currentProgram: [-1],
        programs: [],
        podfiles: []
    },
    methods: {
        /**
         * Checks whether the specified object is null or undefined.
         * @param {any} obj The object to check.
         */
        isNull: function(obj) {
			return obj === undefined || obj === null;	
        },
        
        /**
         * Fetches JSON data from the specified URL.
         * @param {string} URL The URL where to fetch the data from.
         */
        fetchData: async function(URL) {
            if(this.isNull(URL) || typeof URL !== "string") { throw "'URL' must be a string!"; }
            return await (await fetch(URL)).json();
        },
        
        /**
         * Adds a channel to the favourites if it does not already exists.
         * @param {any} channel The channel to add.
         */
		addFavouriteChannel: function(channel) {
			if(this.isNull(channel) || this.isNull(channel.id)) { return; }

			var index = dataStore.data.favouriteChannels.indexOf(channel.id);
            if(index >= 0) { return; }
            
            dataStore.data.favouriteChannels.push(channel.id);
            window.localStorage.setItem("favouriteChannels", dataStore.data.favouriteChannels);
        },

        /**
         * Removes the specified channel from the favourites.
         * @param {any} channel The channel to remove.
         */
		removeFavouriteChannel: function(channel) {
			if(this.isNull(channel) || this.isNull(channel.id)) { return; }

			var index = dataStore.data.favouriteChannels.indexOf(channel.id);
            if(index < 0) { return; }
            
            dataStore.data.favouriteChannels.splice(index, 1);
            window.localStorage.setItem("favouriteChannels", dataStore.data.favouriteChannels);
        },

        addFavouriteProgram: function(program) {
            if(this.isNull(program) || this.isNull(program.id)) { return; }

            var index = dataStore.data.favouritePrograms.indexOf(program.id);
            if(index >= 0) { return; }

            dataStore.data.favouritePrograms.push(program.id);
            window.localStorage.setItem("favouritePrograms", dataStore.data.favouritePrograms);
        },

        removeFavouriteProgram: function(program) {
            if(this.isNull(program) || this.isNull(program.id)) { return; }

            var index = dataStore.data.favouritePrograms.indexOf(program.id);
            if(index < 0) { return; }

            dataStore.data.favouritePrograms.splice(index, 1);
            window.localStorage.setItem("favouritePrograms", dataStore.data.favouritePrograms);
        },

        changeChannel: async function(id) {
            if(this.isNull(id)) { throw "'id' must not be null!"; }

            if(typeof id !== "string" && typeof id !== "number") {
                throw "'id' must be an integer or a string!";
            }

            dataStore.data.currentChannel.splice(0);
            dataStore.data.currentChannel.push(id);

            let programs = (await this.getPrograms(dataStore.data.currentChannel[0])).programs;

            dataStore.data.programs.splice(0);
            for(var i = 0; i < programs.length; i++) {
                dataStore.data.programs.push(programs[i]);
            }
        },

        changeProgram: async function(pid) {
            if(this.isNull(pid)) { throw "'pid' must not be null!"; }

            if(typeof pid !== "string" && typeof pid !== "number") {
                throw "'pid' must be an integer or a string!";
            }

            dataStore.data.currentProgram.splice(0);
            dataStore.data.currentProgram.push(pid);

            let podfiles = (await this.getPodfiles(pid)).podfiles;

            dataStore.data.podfiles.splice(0);
            for(var i = 0; i < podfiles.length; i++) {
                dataStore.data.podfiles.push(podfiles[i]);
            }
        },

        getPodfiles: async function(pid) {
            if(dataStore.methods.isNull(pid) || (typeof pid !== "string" && typeof pid !== "number")) { 
                throw "'pid' must be an integer or a string!";
            }

            const URL = "https://api.sr.se/api/v2/podfiles?programid=" + pid.toString() + "&format=json&pagination=true";
            return (await dataStore.methods.fetchData(URL));
        },

        getPrograms: async function(id) {
            if(dataStore.methods.isNull(id) || (typeof id !== "string" && typeof id !== "number")) { 
                throw "'id' must be an integer or a string!";
            }

            const URL = "https://api.sr.se/api/v2/programs/?channelid=" + id.toString() + "&format=json&pagination=false";
            return (await dataStore.methods.fetchData(URL));
        }
    }
};

export default dataStore;