<template>
	<div id="channel">
		<h1>Channel</h1>
		<div style="margin-bottom: 16px">
			<a href="javascript:void(0)" onclick="document.getElementById('channel').classList.remove('favourites')">Channels</a> | 
			<a href="javascript:void(0)" onclick="document.getElementById('channel').classList.add('favourites')">Favourites</a>
		</div>
		<div class="channels">
			<div v-for="channel in channels" :key="channel.id" class="channel" @click="changeChannel(channel)">
				<div><img :src="channel.image" alt="channel"></div>
				<div>{{channel.name}}</div>
				<div onclick="event.stopPropagation(); event.preventDefault();">
					<a href="javascript:void(0)" @click="toggleFavourite(channel)">
						<img alt="favourite" src="../assets/silkicons/star.png" :class="{'notFavourite': favourites.indexOf(channel.id) < 0}"/>
					</a>
				</div>
			</div>
		</div>
		<div class="favourites">
			<div v-for="(value, key) in favourites" :key="`favorites-${key}`" class="favourite" @click="changeChannel(findChannel(value))">
				<div><img :src="findChannel(value).image" alt="channel"></div>
				<div>{{findChannel(value).name}}</div>
				<div onclick="event.stopPropagation(); event.preventDefault();">
					<a href="javascript:void(0)" @click="removeFavourite(value)">
						<img alt="remove" src="../assets/silkicons/cross.png"/>
					</a>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import dataStore from "../store/dataStore.js";

export default {
	data: function() {
		return {
			favourites: dataStore.data.favouriteChannels,
			channels: []
		};
	},
	methods: {
		isNull: function(obj) {
			return obj === undefined || obj === null;
		},
		addFavourite: function(channel) {
			dataStore.methods.addFavouriteChannel(channel);
		},
		removeFavourite: function(channel) {
			dataStore.methods.removeFavouriteChannel(channel);
		},
		toggleFavourite: function(channel) {
			if(this.favourites.indexOf(channel.id) < 0) {
				this.addFavourite(channel);
			}
			else {
				this.removeFavourite(channel);
			}
		},
		changeChannel: async function(channel) {
			await dataStore.methods.changeChannel(channel.id);
		},
		findChannel: function(id) {
			for(var i = 0; i < this.channels.length; i++) {
				if(this.channels[i].id == id) {
					return this.channels[i];
				}
			}
			return null;
		}
	},
	created: async function() {
		const URL = "https://api.sr.se/api/v2/channels/?format=json&pagination=false";
		this.channels = (await dataStore.methods.fetchData(URL)).channels;
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	#channel.favourites .channels
	{
		display: none;
	}

	#channel:not(.favourites) .favourites
	{
		display: none;
	}

	.channel,
	.favourite
	{
		position: relative;
		height: 100px;
		border-bottom: 1px solid #CCCCCC;
		transition: background 0.15s ease-in-out;
	}

	.channel:hover,
	.favourite:hover
	{
		background: #FFF8E0;
	}

	.channel:first-child,
	.favourite:first-child
	{
		border-top: 1px solid #CCCCCC;
	}

	.channel > div:nth-child(1) img,
	.favourite > div:nth-child(1) img:first-child
	{
		width: 100px;
		height: 100px;
	}

	.channel > div:nth-child(1),
	.favourite > div:nth-child(1)
	{
		position: absolute;
		top: 0;
		left: 0;
	}

	.channel > div:nth-child(2),
	.favourite > div:nth-child(2)
	{
		position: absolute;
		top: 8px;
		left: 108px;
	}

	.channel > div:nth-child(3),
	.favourite > div:nth-child(3)
	{
		position: absolute;
		top: 8px;
		right: 8px;
	}

	.channel > div:nth-child(3) img,
	.favourite > div:nth-child(3) img
	{
		width: 16px;
		height: 16px;
	}

	.channel > div:nth-child(3) img.notFavourite
